import React from "react";
import CenteredWrapper from "../containers/components/micro_components/centeredWrapper";
const About = () => {
    return (
     
        <div style={{textAlign: 'left', margin: '30px', maxWidth: '500px'}}>
        <h1>Om Pycipedia</h1>
        <p style={{fontSize: '16px'}}>Pycipedia r mtesplatsen fr familjebehandlare och boendestdjare som vill dela och ta del av material. P Pycipedia r tanken att du som loggat in ska lgga in material som du gjort och ta del av material andra tagit fram. Det kan vara ett bildstd som du tycker var framgngsrikt i en familj, en film du anvnder dig av i mtet med klient eller forskning kring socialt arbete. </p>

<p style={{fontSize: '16px'}}>PYCIPEDIA r fr nrvarande i en utvecklings- och testfas vilket betyder att viss funktionalitet kan ndras, lggas till eller tas bort baserad p input som vi fr frn dom som anvnder PYCIPEDIA. Tveka inte att skicka dina synpunkter p PYCIPEDIA till oss.</p>

<p style={{fontSize: '16px'}}>PYCIPEDIA r utvecklat p IT Universitetet i Kpenhamn baserat p bland annat co-design aktiviteter tillsammans med socialarbetare och PYC utbildare. Arbetet med PYCIPEDIA r del av ett strre samarbetsprojekt mellan IT Universitetet i Kpenhamn, Kungliga Tekniska Hgskolan i Stockholm och FoU centrum i stergtland. Besk socialcare.itu.dk fr mer information om samarbetet och dess partner.</p>
        
        <h4>Kontakta grna Erik Grnvall, lektor p IT Universitetet med synpunkter, frgor etc via email: erig@itu.dk.</h4>
            </div>
         
    );
}
    
export default About;
