import React, {useState, useEffect} from 'react';


const GridIcon = ({ chosen, size="50" }) => {
    
    const [hasGrid, setHasGrid] = useState(chosen);


    useEffect(() => {
        setHasGrid(chosen);
    }, [chosen])


    const handleClick = () => {
        setHasGrid(!hasGrid);
        //onclick(!hasGrid);
    }

    return (
        <div className="grid-icon" onClick={handleClick}>

            {grid(hasGrid, size)}
        </div>
    )

 

}

